document.addEventListener('DOMContentLoaded', function() { 
  

  let widgets = document.querySelector('.header__menu');
  widgets.addEventListener('click',function(){
    setTimeout(() => this.classList.toggle('active') , 10 );
  });

  document.addEventListener('click', function(event) {
    let widgetsBlock = document.querySelector('.header__widgets');
    if (event.target != widgetsBlock && widgets.classList.contains('active')) { 
      widgets.classList.remove('active');
    } else {
      return false;
    }
  });

  let addAccount = document.querySelector('.notification__item--account'),
      modalAccount = document.querySelector('.signIn__modal');
  
  addAccount.addEventListener('click', () => {
    modalAccount.classList.toggle('active');
  });
  //Sidebar
  let sidebar = document.querySelector('.sidebar'),
      hideSidebar = document.querySelector('.hide__btn');
  
  hideSidebar.addEventListener('click', function() {
    sidebar.classList.toggle('active');
  });

  let mainElem = document.querySelector('.files__list__main'),
      mainGroup = document.querySelector('.files__list'),
      innerElems = document.querySelectorAll('.files__list__sub');
  
  mainElem.addEventListener('click',function(){
    this.classList.toggle('active');
    mainGroup.classList.toggle('active');
  });

  innerElems.forEach( el => {
    el.addEventListener('click',function(e) {
      e.stopPropagation();
      this.classList.toggle('active');
    });
  });

  let contentTabs = document.querySelectorAll('.contents__tab');
  contentTabs.forEach( el => {
    el.addEventListener('click',function() {
      contentTabs.forEach( elem => elem.classList.remove('active'));
      this.classList.add('active');
    });
  });

  let openFiles = document.querySelectorAll('.files__list__inner');
  openFiles.forEach( el => {
    el.addEventListener('click',function(e){
      e.stopPropagation();
    });
  });

  let deleteTabs = document.querySelectorAll('.delete__tab');
  deleteTabs.forEach( el => {
    el.addEventListener('click',function(){
      this.parentElement.remove();
    });
  });

  let lvlItem = document.querySelectorAll('.lvl__list');

  lvlItem.forEach( el => {
    el.querySelectorAll('li').forEach( elInner => {
      elInner.addEventListener('click',function(){
        el.querySelectorAll('li').forEach( elElem => elElem.classList.remove('active'));
        this.classList.toggle('active');
      });
    });
  });
  
  let closeModal = document.querySelector('.closeModal'),
      modal = document.querySelector('.modal'),
      overlay = document.querySelector('.overlay');
  
  closeModal.addEventListener('click',function() {
    modal.classList.remove('active');
    overlay.classList.remove('active');
  });
  
  // var cssLink = document.createElement("link");
  // cssLink.href = "styles/main.min.css";  
  // cssLink.rel = "stylesheet";
  // cssLink.type = "text/css";
  // var frame1 = document.querySelector('#iframe1');
  // frame1.contentDocument.head.appendChild(cssLink); 

  let customEl = document.querySelector('.custom__misc__main'),
      customElMain = document.querySelector('.custom__dialog__misc');
  customEl.addEventListener('click',function() {
    this.classList.toggle('active');
    customElMain.classList.toggle('active');
  });

});